@font-face {
	font-family: 'Circe';
	src: url('../fonts/Circe_ExtraBold/Circe_ExtraBold.eot');
	src: local('Circe_ExtraBold'), local('../fonts/Circe_ExtraBold/Circe_ExtraBold'),
		url('../fonts/Circe_ExtraBold/Circe_ExtraBold.eot?#iefix') format('embedded-opentype'),
		url('../fonts/Circe_ExtraBold/Circe_ExtraBold.woff') format('woff'),
		url('../fonts/Circe_ExtraBold/Circe_ExtraBold.ttf') format('truetype');
	font-weight: 800;
	font-style: normal;
	font-display: swap;
}
